<template>
    <div class="validate">
        <div class="content">
            <h1>是否立即验证?</h1>
            <div class="tips">验证需提交相关证明信息并等待几个工作日且完成验证前将冻结部分功能</div>
            <button @click="validate">立即验证</button>
            <button @click="skip">跳过</button>
        </div>
    </div>
</template>

<script>
// import { getCookie } from '../../../static/js/cookieUtil'
export default {
  name: 'validate',
  methods: {
    validate () {
      if (localStorage.getItem('userType') === 'T') {
        this.$router.push('/home/enroll/coachStep1')
      } else if (localStorage.getItem('userType') === 'M') {
        this.$router.push('/home/enroll/gymAuthentication')
      }
    },
    skip () {
      this.$router.push('/home/index')
    }
  }
}
</script>

<style scoped>
  * {
      margin: 0;
      padding: 0;
  }

  .content {
      width: calc(100% - 1.26667rem);
      margin: 0 auto;
      margin-top: 5.01333rem;
  }
  .content h1 {
      font-size: 0.48rem;
  }

  .tips {
      font-size: 0.426667rem;
      text-align: start;
      font-size: 0.373333rem;
      margin-top: 0.3rem;
      line-height: 0.6rem;
  }

  .content button {
    display: block;
    width: 100%;
    background: #ffd200;
    border: none;
    outline: none;
    margin: 1.09333rem auto;
    font-size: 0.426667rem;
    color: #333333;
    height: 1.3333rem;
    border-radius: 0.15rem;
    font-weight: 400;
  }
</style>



